html,
body
{
  margin:0;
  padding:0;
  width:100%;
  height:100%;
}

div {
  margin:0;
  padding:0;

}

div#mapContainer{
  width:100%;
  height:calc(100% - 150px);
}

path {
  fill: #ccc;
  stroke: #fff;
  stroke-width: .5px;
}

path.active {
  fill: green;
}
 
circle {
  /* fill: #fff;
  fill-opacity: 0.9;
  fill: auto;*/
  stroke: #111;
}

circle.active {
  stroke: #0A98F5;
  stroke-width: 5px;
}

div.tooltip {	
    position: absolute;			
    text-align: center;			
    width: auto;					
    height: 28px;					
    padding: 2px;				
    font: 12px sans-serif;		
    background: burlywood;	
    border: 0px;		
    border-radius: 8px;			
    pointer-events: none;			
}

body {
  font-family: "avenir next", Arial, sans-serif;
  font-size: 12px;
  color: #696969;
}

#play-button {
  position: relative;
  top: 0px;
  left: 0px;
  background: #f08080;
  padding-right: 26px;
  border-radius: 3px;
  border: none;
  color: white;
  margin: 0;
  padding: 0 12px;
  width: 60px;
  cursor: pointer;
  height: 30px;
}

#play-button:hover {
  background-color: #696969;
}

.ticks {
  font-size: 10px;
}

.track,
.track-inset,
.track-overlay {
  stroke-linecap: round;
}

.track {
  stroke: #000;
  stroke-opacity: 0.3;
  stroke-width: 10px;
}

.track-inset {
  stroke: #dcdcdc;
  stroke-width: 8px;
}

.track-overlay {
  pointer-events: stroke;
  stroke-width: 50px;
  stroke: transparent;
  cursor: pointer;
}

.handle {
  fill: #fff;
  stroke: #000;
  stroke-opacity: 0.5;
  stroke-width: 1.25px;
}